.header{
height: 60px;
display: flex;
align-items: center;
background-color: black;
position: sticky;
top: 0;
z-index: 100;
}
.header__logo{
width: 100px;
object-fit:  contain;
margin: 0 20px;
margin-top: 18px;
}
.header__search{
display: flex;
flex: 1;
align-items: center;
border-radius: 24px;
}
.header__searchInput{
height: 12px;
padding: 10px;
border: none;
width: 100%;
}
.header__searchIcon{
    padding: 5px;
    height: 22px;
    background-color: coral;
}
.header__nav{
display: flex;
justify-content: space-evenly;
}
.header__option{
display: flex;
flex-direction: column;
margin-left: 10px;
margin-right: 10px;
color: white;
}
.header__optionBasket{
display: flex;
align-items: center;
color: white;
}
.header__basketCount{
margin-left: 10px;
margin-right: 10px;
}
.header__optionLineTWo
{
    font-size: large;
    font-weight: 300;
}
.header__optionLineOne{
font-size: small;
}